import React, { useState, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { useMutation } from '@apollo/client'
import {
  DialogContent,
  DialogActions,
  Typography,
  FormControl,
  Alert,
  AlertTitle,
} from '@mui/material'
import LoadingButton from '@mui/lab/LoadingButton'
import { Label, Modal, ModalTitle, StyledOutlinedInput } from '../../styled'
import GroupSelect from './GroupSelect'
import ContactSelect from './ContactSelect'
import { Group } from 'state/group/actions'
import { Task } from 'state/task/actions'
import { GET_TASKS, UPDATE_TASK } from 'constants/graphql'

interface State {
  name: string
  platform: string
  url: string
  price: string
  remark: string
  groupId?: string
  contactId?: string
  group?: { id: string, name: string }
  contact?: { id: string, name: string }
}

export default function UpdateTaskModal({ open, value, onChange, onDismiss }: {
  open: boolean
  value?: Task
  onChange?: (value: Task) => void
  onDismiss?: () => void
}) {
  const { t } = useTranslation()
  const [values, setValues] = useState<State>({
    name: '',
    platform: '',
    url: '',
    price: '',
    remark: '',
    groupId: undefined ,
    contactId: undefined,
    group: undefined ,
    contact: undefined,
  })

  useEffect(() => {
    if (value && open) { 
      setValues({ 
        ...value, 
        groupId: value.group?.id,
        contactId: value.contact?.id,
      })
    }
  }, [value, open])

  const [updateTask, { data, loading, error }] = useMutation(UPDATE_TASK, {
    onCompleted: () => {
      onChange && onChange(data.updateTask)
      handleClose()
    },
    refetchQueries: [
      { query: GET_TASKS },
    ],
  })

  const handleChange = (prop: keyof State) => (event: React.ChangeEvent<HTMLInputElement>) => {
    setValues({ ...values, [prop]: event.target.value })
  }

  const handleGroupChange = (value: Group | null) => {
    setValues({ ...values, groupId: value ? value.id : '' })
  }

  const handleContactChange = (value: {id: string} | null) => {
    setValues({ ...values, contactId: value?.id ?? undefined })
  }

  const handleOK = () => {
    if (value) {
      const { id } = value
      const { name,  platform, url, price, remark, groupId, contactId } = values
      
      updateTask({ variables: { 
        "id": id, 
        "input": { name, platform, url, price, remark, groupId, contactId }
      }})
    }
  }

  const handleClose = () => {
    onDismiss && onDismiss()
  }

  return (
      <Modal
        aria-labelledby="gruop-modal-title"
        maxWidth={'sm'}
        fullWidth={true}
        open={open}
        onClose={handleClose}
      >
        <ModalTitle id="gruop-modal-title" onClose={handleClose}>
          {t('修改任务')}
        </ModalTitle>
        <DialogContent dividers sx={{ '> .MuiFormControl-root': { width: '100%' }}}>
          <Typography component={Label}>{t('名称')}</Typography>
            <FormControl variant="outlined">
              <StyledOutlinedInput
                type="text"
                placeholder={t('填写任务名称')}
                value={values.name}
                onChange={handleChange("name")}
              />
            </FormControl>
            <Typography component={Label}>{t('平台')}</Typography>
            <FormControl variant="outlined">
              <StyledOutlinedInput
                type="text"
                placeholder={t('填写平台名称')}
                value={values.platform}
                onChange={handleChange("platform")}
              />
            </FormControl>
            <Typography component={Label}>{t('URL')}</Typography>
            <FormControl variant="outlined">
              <StyledOutlinedInput
                type="text"
                placeholder={t('填写URL')}
                value={values.url}
                onChange={handleChange("url")}
              />
            </FormControl>
            <Typography component={Label}>{t('价格')}</Typography>
            <FormControl variant="outlined">
              <StyledOutlinedInput
                type="text"
                placeholder={t('价格（可选）')}
                value={values.price}
                onChange={handleChange("price")}
              />
            </FormControl>
            <Typography component={Label}>{t('备注')}</Typography>
            <FormControl variant="outlined">
              <StyledOutlinedInput
                multiline={true}
                minRows={2}
                maxRows={5}
                placeholder={t('填写任务备注')}
                value={values.remark}
                onChange={handleChange("remark")}
              />
            </FormControl>
            <Typography component={Label}>{t('分组')}</Typography>
            <FormControl variant="outlined">
              <GroupSelect value={values.group ? { ...values.group, createdTime: 0 } : undefined} onChange={handleGroupChange} />
            </FormControl>
              <Typography component={Label}>{t('联系人')}</Typography>
            <FormControl variant="outlined">
              <ContactSelect value={values.contact} onChange={handleContactChange} />
            </FormControl>
        </DialogContent>
        <DialogActions>
          {error && (
            <Alert severity="error">
              <AlertTitle>Error</AlertTitle>
              {error.message}
            </Alert>
          )}
          <LoadingButton 
            autoFocus
            variant="contained" 
            color="secondary"
            loading={loading}
            onClick={handleOK}
          >
            {t('确定')}
          </LoadingButton>
        </DialogActions>
      </Modal>
  )
}