import React, { useState, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { useMutation } from '@apollo/client'
import {
  styled,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  IconButton,
  Typography,
  FormControl,
  Alert,
  AlertTitle,
} from '@mui/material'
import LoadingButton from '@mui/lab/LoadingButton'
import { X } from 'react-feather'
import { Label, StyledOutlinedInput } from '../../styled'
import { Group } from 'state/group/actions'

import { GET_GROUPS, CREATE_GROUP, UPDATE_GROUP } from 'constants/graphql'

const BootstrapDialog = styled(Dialog)(({ theme }) => ({
  
  '& .MuiDialogContent-root': {
    padding: theme.spacing(2),
  },
  '& .MuiDialogActions-root': {
    padding: theme.spacing(1),
  },
}));

export interface DialogTitleProps {
  id: string
  children?: React.ReactNode
  onClose: () => void
}

const BootstrapDialogTitle = (props: DialogTitleProps) => {
  const { children, onClose, ...other } = props;

  return (
    <DialogTitle sx={{ m: 0, p: 2 }} {...other}>
      {children}
      {onClose ? (
        <IconButton
          aria-label="close"
          onClick={onClose}
          sx={{
            position: 'absolute',
            right: 8,
            top: 8,
            color: (theme) => theme.palette.grey[500],
          }}
        >
          <X />
        </IconButton>
      ) : null}
    </DialogTitle>
  )
}

export interface ButtonProps {
  label?: string
  value: Group
  onCompleted?: (value: Group) => void
  onError?: (msg: string) => void
}

const CreateButton = (props: ButtonProps) => {
  const { label, value, onCompleted, onError } = props

  const [createGroup, { data, loading, error }] = useMutation(CREATE_GROUP, {
    onCompleted: () => {
      onCompleted && onCompleted(data.createGroup)
    },
    refetchQueries: [
      {query: GET_GROUPS},
    ],
  })

  const handleOK = () => {
    createGroup({ variables: { name: value.name } })
  }

  useEffect(() => error && onError && onError(error.message), [error, onError])

  return (
    <LoadingButton 
        autoFocus
        variant="contained" 
        color="secondary"
        loading={loading}
        onClick={handleOK}
      >
        {label ?? 'OK'}
      </LoadingButton>
  )
}

const UpdateButton = (props: ButtonProps) => {
  const { label, value, onCompleted, onError } = props

  const [updateGroup, { data, loading, error }] = useMutation(UPDATE_GROUP, {
    onCompleted: () => {
      onCompleted && onCompleted(data.updateGroup)
    },
    refetchQueries: [
      {query: GET_GROUPS},
    ],
  })

  const handleOK = () => {
    updateGroup({ variables: { id: value.id, name: value.name } })
  }

  useEffect(() => error && onError && onError(error.message), [error, onError])

  return (
    <LoadingButton 
        autoFocus
        variant="contained" 
        color="secondary"
        loading={loading}
        onClick={handleOK}
      >
        {label ?? 'OK'}
      </LoadingButton>
  )
}

export default function GroupModal({ open, value, onChange, onDismiss }: {
  open: boolean
  value?: Group
  onChange?: (value: Group) => void
  onDismiss?: () => void
}) {
  const { t } = useTranslation()
  const [error, setError] = useState<string | undefined>(undefined) 
  const [values, setValues] = useState<Group>({ id: '', name: '', createdTime: 0 })

  useEffect(() => {
    if (open && value) {
      setValues({ ...value})
    } else {
      setValues({ id: '', name: '', createdTime: 0 })
    }
  }, [value, open])

  const handleChange = (prop: keyof Group) => (event: React.ChangeEvent<HTMLInputElement>) => {
    setValues({ ...values, [prop]: event.target.value })
  }

  // const handleKeyUp = (event: React.KeyboardEvent<HTMLInputElement>) => {
  //   if (event && event.key === 'Enter') {
  //     handleOK()
  //   }
  // }

  const handleCompleted = (value: Group) => {
    onChange && onChange(value)
    handleClose()
  }

  const handleClose = () => {
    onDismiss && onDismiss()
  }

  const handleError = (err: string) => {
    setError(err)
  }

  return (
      <BootstrapDialog
        aria-labelledby="gruop-modal-title"
        maxWidth={'sm'}
        fullWidth={true}
        open={open}
        onClose={handleClose}
      >
        <BootstrapDialogTitle id="gruop-modal-title" onClose={handleClose}>
          {value ? t('修改分组') : t('创建分组')}
        </BootstrapDialogTitle>
        <DialogContent dividers>
          <Typography component={Label}>{t('名称')}</Typography>
          <FormControl variant="outlined" sx={{ width: '100%' }}>
            <StyledOutlinedInput
              autoFocus
              type="text"
              value={values.name}
              placeholder={t('填写名称')}
              onChange={handleChange('name')}
            />
          </FormControl>
        </DialogContent>
        <DialogActions>
          {error && (
            <Alert severity="error">
              <AlertTitle>Error</AlertTitle>
              {error}
            </Alert>
          )}
          {value ? (
            <UpdateButton 
              label={t('确定')}
              value={values}
              onCompleted={handleCompleted}
              onError={handleError}
            />
          ):(
            <CreateButton 
              label={t('确定')}
              value={values}
              onCompleted={handleCompleted}
              onError={handleError}
            />
          )}
          
        </DialogActions>
      </BootstrapDialog>
  )
}
