import React, { useState, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { useMutation } from '@apollo/client'
import {
  DialogContent,
  DialogActions,
  Typography,
  FormControl,
  Alert,
  AlertTitle,
} from '@mui/material'
import LoadingButton from '@mui/lab/LoadingButton'
import { Label, Modal, ModalTitle, StyledOutlinedInput } from '../../styled'
import GroupSelect from './GroupSelect'
import CountrySelect from './CountrySelect'
import { Contact, Country } from 'state/contact/actions'
import { Group } from 'state/group/actions'
import { GET_CONTACTS, CREATE_CONTACT, UPDATE_CONTACT } from 'constants/graphql'

interface State {
  name: string
  groupId?: string
  country?: Country
  numbers: string[]
}

function UpdateModal({ open, value, onChange, onDismiss }: {
  open: boolean
  value: Contact
  onChange?: (value: Contact) => void
  onDismiss?: () => void
}) {
  const { t } = useTranslation()
  const group: Group | undefined = value.group ? { ...value.group, createdTime: 0 } : undefined
  const [values, setValues] = useState<State>({
    name: value.name,
    groupId: value.group?.id,
    country: value.country,
    numbers: value.numbers,
  })

  useEffect(() => {
    if (open) {
      setValues({ ...value })
    }
  }, [value, open])

  const [updateContact, { data, loading, error }] = useMutation(UPDATE_CONTACT, {
    onCompleted: () => {
      onChange && onChange(data.updateContact)
      handleClose()
    },
    refetchQueries: [
      {query: GET_CONTACTS},
      'GetContacts',
    ],
  })

  const handleChange = (prop: keyof State) => (event: React.ChangeEvent<HTMLInputElement>) => {
    if (prop === 'numbers') {
      setValues({ ...values, [prop]: event.target.value.split("\n") })
    } else {
      setValues({ ...values, [prop]: event.target.value })
    }
  }

  const handleKeyUp = (event: React.KeyboardEvent<HTMLInputElement>) => {
    if (event && event.key === 'Enter') {
      handleOK()
    }
  }

  const handleGroupChange = (value: Group | null) => {
    setValues({ ...values, groupId: value ? value.id : '' })
  }

  const handleCountryChange = (value: Country | null) => {
    setValues({ ...values, country: value ?? undefined })
  }

  const handleOK = () => {
    const { name, groupId, country, numbers } = values
    updateContact({ variables: { "id": value.id, "input": { name, groupId, country, numbers }}})
  }

  const handleClose = () => {
    onDismiss && onDismiss()
  }

  return (
      <Modal
        aria-labelledby="gruop-modal-title"
        maxWidth={'sm'}
        fullWidth={true}
        open={open}
        onClose={handleClose}
      >
        <ModalTitle id="gruop-modal-title" onClose={handleClose}>
          {t('修改联系人')}
        </ModalTitle>
        <DialogContent dividers sx={{ '> .MuiFormControl-root': { width: '100%' }}}>
          <Typography component={Label}>{t('名称')}</Typography>
          <FormControl variant="outlined">
            <StyledOutlinedInput
              autoFocus
              type="text"
              value={values.name}
              placeholder={t('填写名称')}
              onChange={handleChange('name')}
              onKeyUp={handleKeyUp}
            />
          </FormControl>
          <Typography component={Label}>{t('分组')}</Typography>
          <FormControl variant="outlined">
            <GroupSelect value={group} onChange={handleGroupChange} />
          </FormControl>
          <Typography component={Label}>{t('国家')}</Typography>
          <FormControl variant="outlined">
            <CountrySelect value={values.country} onChange={handleCountryChange} />
          </FormControl>
          <Typography component={Label}>{t('号码')}</Typography>
          <FormControl variant="outlined">
            <StyledOutlinedInput
              multiline={true}
              minRows={5}
              maxRows={20}
              placeholder={t('号码|月份|年份|CVV')}
              value={values.numbers.join('\n')}
              onChange={handleChange('numbers')}
            />
          </FormControl>
        </DialogContent>
        <DialogActions>
          {error && (
            <Alert severity="error">
              <AlertTitle>Error</AlertTitle>
              {error.message}
            </Alert>
          )}
          <LoadingButton 
            autoFocus
            variant="contained" 
            color="secondary"
            loading={loading}
            onClick={handleOK}
          >
            {t('确定')}
          </LoadingButton>
        </DialogActions>
      </Modal>
  )
}

function CreateModal({ open, onChange, onDismiss }: {
  open: boolean
  onChange?: (value: Contact) => void
  onDismiss?: () => void
}) {
  const { t } = useTranslation()
  const [values, setValues] = useState<State>({
    name: '',
    groupId: '',
    country: undefined,
    numbers: [],
  })

  useEffect(() => {
    if (open) {
      setValues({ 
        name: '',
        groupId: '',
        country: undefined,
        numbers: [], 
      })
    }
  }, [open])

  const [createContact, { data, loading, error }] = useMutation(CREATE_CONTACT, {
    onCompleted: () => {
      onChange && onChange(data.createContact)
      handleClose()
    },
    refetchQueries: [
      {query: GET_CONTACTS},
      'GetContacts',
    ],
  })

  const handleChange = (prop: keyof State) => (event: React.ChangeEvent<HTMLInputElement>) => {
    if (prop === 'numbers') {
      setValues({ ...values, [prop]: event.target.value.split("\n") })
    } else {
      setValues({ ...values, [prop]: event.target.value })
    }
  }

  const handleKeyUp = (event: React.KeyboardEvent<HTMLInputElement>) => {
    if (event && event.key === 'Enter') {
      handleOK()
    }
  }

  const handleGroupChange = (value: Group | null) => {
    setValues({ ...values, groupId: value ? value.id : '' })
  }

  const handleCountryChange = (value: Country | null) => {
    setValues({ ...values, country: value ?? undefined })
  }

  const handleOK = () => {
    createContact({ variables: { ...values } })
  }

  const handleClose = () => {
    onDismiss && onDismiss()
  }

  return (
      <Modal
        aria-labelledby="gruop-modal-title"
        maxWidth={'sm'}
        fullWidth={true}
        open={open}
        onClose={handleClose}
      >
        <ModalTitle id="gruop-modal-title" onClose={handleClose}>
          {t('创建联系人')}
        </ModalTitle>
        <DialogContent dividers sx={{ '> .MuiFormControl-root': { width: '100%' }}}>
          <Typography component={Label}>{t('名称')}</Typography>
          <FormControl variant="outlined">
            <StyledOutlinedInput
              autoFocus
              type="text"
              value={values.name}
              placeholder={t('填写名称')}
              onChange={handleChange('name')}
              onKeyUp={handleKeyUp}
            />
          </FormControl>
          <Typography component={Label}>{t('分组')}</Typography>
          <FormControl variant="outlined">
            <GroupSelect onChange={handleGroupChange} />
          </FormControl>
          <Typography component={Label}>{t('国家')}</Typography>
          <FormControl variant="outlined">
            <CountrySelect value={values.country} onChange={handleCountryChange} />
          </FormControl>
          <Typography component={Label}>{t('号码')}</Typography>
          <FormControl variant="outlined">
            <StyledOutlinedInput
              multiline={true}
              minRows={5}
              maxRows={20}
              placeholder={t('号码|月份|年份|CVV')}
              value={values.numbers.join('\n')}
              onChange={handleChange('numbers')}
            />
          </FormControl>
        </DialogContent>
        <DialogActions>
          {error && (
            <Alert severity="error">
              <AlertTitle>Error</AlertTitle>
              {error.message}
            </Alert>
          )}
          <LoadingButton 
            autoFocus
            variant="contained" 
            color="secondary"
            loading={loading}
            onClick={handleOK}
          >
            {t('确定')}
          </LoadingButton>
        </DialogActions>
      </Modal>
  )
}

export default function ContactModal({ open, value, onChange, onDismiss }: {
  open: boolean
  value?: Contact
  onChange?: (value: Contact) => void
  onDismiss?: () => void
}) {
  
  return value ? (
    <UpdateModal open={open} value={value} onChange={onChange} onDismiss={onDismiss} />
  ) : (
    <CreateModal open={open} onChange={onChange} onDismiss={onDismiss} />
  )
}
