import React, { useState, useMemo, useEffect } from 'react'
import TextField from '@mui/material/TextField'
import Autocomplete from '@mui/material/Autocomplete'
import CircularProgress from '@mui/material/CircularProgress'
import { useContactOptions } from 'state/contact/hooks'

interface Option {
  id: string
  name: string
}

export default function ContactSelect({ value, onChange }: { value?: Option; onChange?: (value: Option | null) => void }) {
  const [open, setOpen] = useState(false)
  const [options, setOptions] = useState<readonly Option[]>([])
  const loading = open && options.length === 0
  
  const contacts = useContactOptions()
  const sortedContacts = useMemo(() => {
    return contacts ? [...contacts].sort((a, b) => a.name.localeCompare(b.name)) : []
  }, [contacts])

  useEffect(() => {
    let active = true;

    if (!loading) {
      return undefined;
    }

    (async () => {
      // await sleep(1e3)
      console.log(sortedContacts)
      if (active) {
        setOptions([...sortedContacts])
      }
    })()

    return () => {
      active = false;
    }
  }, [loading, sortedContacts])

  useEffect(() => {
    if (!open) {
      setOptions([])
    }
  }, [open])

  const handleChange = (event: React.SyntheticEvent<Element, Event>, value: Option | null) => {
    onChange && onChange(value)
  }

  return (
    <Autocomplete
      disablePortal
      id="contact-select"
      sx={{
        width: '100%',
        '.MuiOutlinedInput-root': {
            padding: 0,
            fontSize: '0.875rem',
        },
        '.MuiOutlinedInput-root .MuiAutocomplete-input': {
            padding: '0.375rem 0.75rem',
        },
        '& .MuiOutlinedInput-notchedOutline': {
            '& > legend': {
            float: 'left !important',
            }
        },
      }}
      open={open}
      onOpen={() => setOpen(true)}
      onClose={() => setOpen(false)}
      onChange={handleChange}
      isOptionEqualToValue={(option, value) => option.id === value.id}
      getOptionLabel={(option) => option.name}
      options={options}
      loading={loading}
      defaultValue={value}
      renderInput={(params) => (
        <TextField
          {...params}
          placeholder={'选择分组'}
          InputProps={{
            ...params.InputProps,
            endAdornment: (
              <React.Fragment>
                {loading ? <CircularProgress color="inherit" size={20} /> : null}
                {params.InputProps.endAdornment}
              </React.Fragment>
            ),
          }}
        />
      )}
    />
  )
}
