import { createAction } from '@reduxjs/toolkit'

export interface Country {
  code: string
  name: string
}

export interface Contact {
  id: string
  name: string
  group?: { id: string, name: string }
  country?: Country
  numbers: string[]
  createdTime: number
}

export const addContact= createAction<{ accountId: string; contact: Contact }>('contact/add')
export const removeContact = createAction<{ accountId: string; contact: Contact }>('contact/remove')
