import React, { useState, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { useMutation } from '@apollo/client'
import {
  DialogContent,
  DialogActions,
  Typography,
  FormControl, 
  TextField,
  Autocomplete,
  Alert,
  AlertTitle,
} from '@mui/material'
import LoadingButton from '@mui/lab/LoadingButton'
import { Label, Modal, ModalTitle, StyledOutlinedInput } from '../../styled'
import { Proxy, Task } from 'state/task/actions'
import { ProxyTypes } from 'state/task/hooks'
import { GET_TASKS, UPDATE_TASK_PROXY } from 'constants/graphql'

interface State {
  proxy: Proxy
  proxyUrl?: string
}

const defalutProxy: Proxy = {
  method: '2',
  type: 'socks5',
  host: '',
  port: '',
  username: undefined ,
  password: undefined,
}

export default function UpdateProxyModal({ open, value, onChange, onDismiss }: {
  open: boolean
  value?: Task
  onChange?: (value: Task) => void
  onDismiss?: () => void
}) {
  const { t } = useTranslation()
  const [values, setValues] = useState<State>({
    proxy: defalutProxy,
    proxyUrl: undefined,
  })

  useEffect(() => {
    if (value && open) {
      setValues({ proxy: value.proxy ?? defalutProxy, proxyUrl: value.proxyUrl })
    }
  }, [value, open])

  const [updateTaskProxy, { data, loading, error }] = useMutation(UPDATE_TASK_PROXY, {
    onCompleted: () => {
      onChange && onChange(data.updateTaskProxy)
      handleClose()
    },
    refetchQueries: [
      { query: GET_TASKS },
    ],
  })

  const handleChange = (prop: keyof State) => (event: React.ChangeEvent<HTMLInputElement>) => {
    setValues({ ...values, [prop]: event.target.value })
  }

  const handleProxyChange = (prop: keyof Proxy) => (event: React.ChangeEvent<HTMLInputElement>) => {
    const { proxy } = values
    setValues({ ...values, proxy: { ...proxy, [prop]: event.target.value} });
  }

  const handleProxyType = (event: React.SyntheticEvent<Element, Event>, value: { value: string } | null) => {
    const { proxy } = values
    setValues({ ...values, proxy: { ...proxy, type: value ? value.value : ''} })
  }

  const handleOK = () => {
    if (value) {
      const { proxy, proxyUrl } = values
      
      updateTaskProxy({ variables: { 
        "id": value.id,  
        "input": { ...proxy },
        "proxyUrl": proxyUrl,
      }})
    }
  }

  const handleClose = () => {
    onDismiss && onDismiss()
  }

  return (
      <Modal
        aria-labelledby="gruop-modal-title"
        maxWidth={'sm'}
        fullWidth={true}
        open={open}
        onClose={handleClose}
      >
        <ModalTitle id="gruop-modal-title" onClose={handleClose}>
          {t('修改代理')}
        </ModalTitle>
        <DialogContent dividers sx={{ '> .MuiFormControl-root': { width: '100%' }}}>
          <Typography component={Label}>{t('代理类型')}</Typography>
          <FormControl variant="outlined">
            <Autocomplete
              disablePortal
              sx={{
                '.MuiOutlinedInput-root': {
                  padding: 0,
                  fontSize: '0.875rem',
                },
                '& .MuiOutlinedInput-notchedOutline': {
                  '& > legend': {
                    float: 'left !important',
                  }
                },
              }}
              options={ProxyTypes}
              renderInput={(params) => <TextField {...params} />}
              defaultValue={ProxyTypes[3]}
              onChange={handleProxyType}
            />
          </FormControl>
          <Typography component={Label}>{t('代理主机')}</Typography>
          <FormControl variant="outlined">
            <StyledOutlinedInput
              type="text"
              placeholder={t('代理主机')}
              value={values.proxy.host}
              onChange={handleProxyChange("host")}
            />
          </FormControl>
          <Typography component={Label}>{t('代理端口')}</Typography>
          <FormControl variant="outlined">
            <StyledOutlinedInput
              type="text"
              placeholder={t('代理端口')}
              value={values.proxy.port}
              onChange={handleProxyChange("port")}
            />
          </FormControl>
          <Typography component={Label}>{t('代理账号')}</Typography>
          <FormControl variant="outlined">
            <StyledOutlinedInput
              type="text"
              placeholder={t('代理账号（可选）')}
              value={values.proxy.username}
              onChange={handleProxyChange("username")}
            />
          </FormControl>
          <Typography component={Label}>{t('代理密码')}</Typography>
          <FormControl variant="outlined">
            <StyledOutlinedInput
              type="text"
              placeholder={t('代理密码（可选）')}
              value={values.proxy.password}
              onChange={handleProxyChange("password")}
            />
          </FormControl>
          <Typography component={Label}>{t('刷新URL')}</Typography>
          <FormControl variant="outlined">
            <StyledOutlinedInput
              type="text"
              placeholder={t('刷新代理的URL（可选）')}
              value={values.proxyUrl}
              onChange={handleChange("proxyUrl")}
            />
          </FormControl>
        </DialogContent>
        <DialogActions>
          {error && (
            <Alert severity="error">
              <AlertTitle>Error</AlertTitle>
              {error.message}
            </Alert>
          )}
          <LoadingButton 
            autoFocus
            variant="contained" 
            color="secondary"
            loading={loading}
            onClick={handleOK}
          >
            {t('确定')}
          </LoadingButton>
        </DialogActions>
      </Modal>
  )
}