import { useCallback, useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useQuery } from '@apollo/client'
import { Account } from 'state/account/actions'
import { useAccountState } from 'state/account/hooks'
import { AppDispatch, AppState } from '../index'
import { Contact, addContact, removeContact } from "./actions"
import { GET_CONTACT_OPTIONS } from 'constants/graphql'

export function useActionHandlers(account?: Account): {
  onAdd: (contact: Contact) => void
  onRemove: (contact: Contact) => void
} {
  const dispatch = useDispatch<AppDispatch>();

  const onAdd = useCallback(
    (contact: Contact) => {
      account && dispatch(addContact({ accountId: account.id, contact }))
    },
    [dispatch, account]
  )

  const onRemove = useCallback(
    (contact: Contact) => {
      account && dispatch(removeContact({ accountId: account.id, contact }))
    },
    [dispatch, account]
  )

  return {
    onAdd,
    onRemove,
  };
}

export function useContact(): { [id: string]: Contact } {
  const { account } = useAccountState()
  const state =  useSelector<AppState, AppState['contact']>(state => state.contact)

  return account ? state[account.id] ?? {} : {}
}

export function useContactOptions(): {id: string, name: string}[] | undefined {
  const [result, setResult] = useState<{id: string, name: string}[] | undefined>(undefined)
  const { data }  = useQuery(GET_CONTACT_OPTIONS)

  useEffect(() => {
    if (data) {
      setResult(data.contacts)
    }
  }, [data])

  return result
}