import { createReducer } from '@reduxjs/toolkit'
import {
  Contact,
  addContact,
  removeContact,
} from './actions'

export interface ContactState {
  [accountId: string]: {
    [id: string]: Contact
  }
}

export const initialState: ContactState = {}

export default createReducer(initialState, builder =>
  builder
    .addCase(addContact, (state, { payload: { accountId, contact } }) => {
      const contacts = state[accountId] ?? {}
      contacts[contact.id] = contact
      state[accountId] = contacts
    })
    .addCase(removeContact, (state, { payload: { accountId, contact } }) => {
      const contacts = state[accountId] ?? {}
      if (!contacts) return
      delete contacts[contact.id]
    })
)
