import { gql } from '@apollo/client'

export const GET_SETTINGS = gql`
  query GetSettings {
    settings
  }
`

export const UPDATE_SETTINGS = gql`
  mutation UpdateSettings($input: Map!) {
    updateSettings(input: $input)
  }
`

export const GET_CONTACTS = gql`
  query GetContacts {
    contacts {
      id
      name
      group {
        id
        name
      }
      country {
        name
        code
      }
      numbers
      createdTime
    }
  }
`

export const GET_CONTACT_OPTIONS = gql`
  query GetContactOptions {
    contacts {
      id
      name
    }
  }
`

export const CREATE_CONTACT = gql`
  mutation CreateContact($name: String!, $groupId: String!, $country: CountryInput, $numbers: [String!]!) {
    createContact(input: {
      name: $name,
      groupId: $groupId,
      country: $country,
      numbers: $numbers
    }) {
      id
      name
      group {
        id
        name
      }
      country {
        name
        code
      }
      numbers
      createdTime
    }
  }
`

export const DELETE_CONTACT = gql`
  mutation DeleteContact($id: ID!) {
    deleteContact(id: $id) {
      id
      name
      group {
        id
        name
      }
      country {
        name
        code
      }
      numbers
      createdTime
    }
  }
`


export const UPDATE_CONTACT = gql`
  mutation UpdateContact($id: ID!, $input: NewContact!) {
    updateContact(id: $id, input: $input) {
      id
      name
      group {
        id
        name
      }
      country {
        name
        code
      }
      numbers
      createdTime
    }
  }
`

export const GET_GROUPS = gql`
  query GetGroups {
    groups {
      id
      name
      createdTime
    }
  }
`

export const CREATE_GROUP = gql`
  mutation CreateGroup($name: String!){
    createGroup(input: $name) {
      id
      name
      createdTime
    }
  }
`

export const DELETE_GROUP = gql`
  mutation DeleteGroup($id: ID!){
    deleteGroup(id: $id) {
      id
      name
      createdTime
    }
  }
`

export const UPDATE_GROUP = gql`
  mutation UpdateGroup($id: ID!, $name: String!){
    updateGroup(id: $id, input: $name) {
      id
      name
      createdTime
    }
  }
`

export const GET_TASKS = gql`
  query GetTasks {
    tasks {
      id
      name
      status
      platform
      url
      price
      remark
      proxy {
        method
        type
        host
        port
        username
        password
      }
      proxyUrl
      group {
        id
        name
      }
      contact {
        id
        name
      }
      country {
        code
        name
      }
      createdTime
    }
  }
`

export const CREATE_TASK = gql`
  mutation CreateTask($name: String!, $platform: String, $url: String!, $price: String, $remark: String, $proxy: ProxyInput, $proxyUrl: String, $groupId: String, $contactId: String, $country: CountryInput) {
    createTask(input: {
      name: $name,
      platform: $platform,
      url: $url,
      price: $price,
      remark: $remark,
      proxy: $proxy,
      proxyUrl: $proxyUrl,
      groupId: $groupId,
      contactId: $contactId,
      country: $country,
    }) {
      id
      name
      status
      platform
      remark
      proxy {
        host
      }
      group {
        id
        name
      }
      contact {
        id
        name
      }
      country {
        code
        name
      }
      createdTime
    }
  }
`

export const DELETE_TASK = gql`
  mutation DeleteTask($id: ID!) {
    deleteTask(id: $id) {
      id
      createdTime
    }
  }
`

export const UPDATE_TASK = gql`
  mutation UpateTask($id: ID!, $input: NewTask!) {
    updateTask(id: $id, input: $input) {
      id
      name
      status
      platform
      url
      price
      remark
      group {
        id
        name
      }
      contact {
        id
        name
      }
      createdTime
    }
  }
`

export const UPDATE_TASK_PROXY = gql`
  mutation UpdateTaskProxy($id: ID!, $input: ProxyInput, $proxyUrl: String) {
    updateTaskProxy(id: $id, input: $input, proxyURL: $proxyUrl) {
      id
      proxy {
        method
        type
        host
        port
        username
        password
      }
      proxyUrl
    }
  }
`

export const UPDATE_TASK_COUNTRY = gql`
  mutation UpdateTaskCountry($id: ID!, $input: CountryInput) {
    updateTaskCountry(id: $id, input: $input) {
      id
      country {
        code
        name
      }
    }
  }
`

export const START_TASK = gql`
  mutation Start($taskID: ID!) {
    start(taskID: $taskID) {
      status
    }
  }
`

export const STOP_TASK = gql`
  mutation Stop($taskID: ID!) {
    stop(taskID: $taskID)
  }
`