import React, { useState, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { useMutation } from '@apollo/client'
import {
  DialogContent,
  DialogActions,
  Typography,
  FormControl,
  Alert,
  AlertTitle,
} from '@mui/material'
import LoadingButton from '@mui/lab/LoadingButton'
import { Label, Modal, ModalTitle } from '../../styled'
import { Country } from 'state/contact/actions'
import { Task } from 'state/task/actions'
import { GET_TASKS, UPDATE_TASK_COUNTRY } from 'constants/graphql'
import CountrySelect from './CountrySelect'

interface State {
  country?: Country
}

export default function UpdateCountryModal({ open, value, onChange, onDismiss }: {
  open: boolean
  value?: Task
  onChange?: (value: Task) => void
  onDismiss?: () => void
}) {
  const { t } = useTranslation()
  const [values, setValues] = useState<State>({
    country: {
      name: '',
      code: '',
    },
  })

  useEffect(() => {
    value && open && setValues({ ...value })
  }, [value, open])

  const [updateTaskCountry, { data, loading, error }] = useMutation(UPDATE_TASK_COUNTRY, {
    onCompleted: () => {
      onChange && onChange(data.updateTaskCountry)
      handleClose()
    },
    refetchQueries: [
      { query: GET_TASKS },
    ],
  })

  const handleChange = (value: Country | null) => {
    setValues({ ...values, country: value ?? undefined })
  }

  const handleOK = () => {
    if (value) {
      const { country } = values
      
      updateTaskCountry({ variables: { 
        "id": value.id,  
        "input": { ...country }
      }})
    }
  }

  const handleClose = () => {
    onDismiss && onDismiss()
  }

  return (
      <Modal
        aria-labelledby="gruop-modal-title"
        maxWidth={'sm'}
        fullWidth={true}
        open={open}
        onClose={handleClose}
      >
        <ModalTitle id="gruop-modal-title" onClose={handleClose}>
          {t('更换国家/地区')}
        </ModalTitle>
        <DialogContent dividers sx={{ '> .MuiFormControl-root': { width: '100%' }}}>
          <Typography component={Label}>{t('国家/地区')}</Typography>
          <FormControl variant="outlined">
            <CountrySelect onChange={handleChange} />
          </FormControl>
        </DialogContent>
        <DialogActions>
          {error && (
            <Alert severity="error">
              <AlertTitle>Error</AlertTitle>
              {error.message}
            </Alert>
          )}
          <LoadingButton 
            autoFocus
            variant="contained" 
            color="secondary"
            loading={loading}
            onClick={handleOK}
          >
            {t('确定')}
          </LoadingButton>
        </DialogActions>
      </Modal>
  )
}